
import Toggle from '@/components/toggle.vue';
import DFieldset from '@/components/ui/d-fieldset.vue';
import {
	closestCharacterSlot,
	ISetFreeMoveMutation,
	ICharacter,
} from '@/store/objectTypes/characters';
import {
	ISetPositionAction,
	ISetObjectPositionMutation,
	IObject,
	ISetHeightAction,
	ISetWidthAction,
	ISetRatioAction,
} from '@/store/objects';
import { ITextBox } from '@/store/objectTypes/textbox';
import { defineComponent, Prop } from 'vue';
import getConstants from '@/constants';
import { rendererLookup } from '@/renderables/textbox';

export default defineComponent({
	components: { Toggle, DFieldset },
	props: {
		obj: {
			required: true,
		} as Prop<IObject>,
	},
	computed: {
		freeMove: {
			get(): boolean {
				return (this.obj! as ICharacter).freeMove;
			},
			set(freeMove: boolean) {
				this.vuexHistory.transaction(() => {
					this.$store.commit('objects/setFreeMove', {
						id: this.obj!.id,
						freeMove,
					} as ISetFreeMoveMutation);
				});
			},
		},
		preserveRatio: {
			get(): boolean {
				return (this.obj! as ICharacter).preserveRatio;
			},
			set(preserveRatio: boolean) {
				this.vuexHistory.transaction(() => {
					this.$store.dispatch('objects/setPreserveRatio', {
						id: this.obj!.id,
						preserveRatio,
					} as ISetRatioAction);
				});
			},
		},
		pos: {
			get(): number {
				return closestCharacterSlot(this.obj!.x);
			},
			set(value: number) {
				this.vuexHistory.transaction(() => {
					this.$store.dispatch('objects/setPosition', {
						id: this.obj!.id,
						x: getConstants().Base.characterPositions[value],
						y: this.obj!.y,
					} as ISetPositionAction);
				});
			},
		},
		x: {
			get(): number {
				return this.obj!.x;
			},
			set(x: number) {
				this.vuexHistory.transaction(() => {
					this.$store.commit('objects/setPosition', {
						id: this.obj!.id,
						x,
						y: this.y,
					} as ISetObjectPositionMutation);
				});
			},
		},
		y: {
			get(): number {
				return this.obj!.y;
			},
			set(y: number) {
				this.vuexHistory.transaction(() => {
					this.$store.commit('objects/setPosition', {
						id: this.obj!.id,
						x: this.x,
						y,
					} as ISetObjectPositionMutation);
				});
			},
		},
		height: {
			get(): number {
				return this.obj!.height;
			},
			set(height: number) {
				this.vuexHistory.transaction(() => {
					this.$store.dispatch('objects/setHeight', {
						id: this.obj!.id,
						height,
					} as ISetHeightAction);
				});
			},
		},
		width: {
			get(): number {
				return this.obj!.width;
			},
			set(width: number) {
				this.vuexHistory.transaction(() => {
					this.$store.dispatch('objects/setWidth', {
						id: this.obj!.id,
						width,
					} as ISetWidthAction);
				});
			},
		},
		allowSize(): boolean {
			const obj = this.obj!;
			if (obj.type === 'textBox') {
				const renderer = rendererLookup[(obj as ITextBox).style];
				return renderer.resizable;
			}
			if (obj.type === 'character' && !(obj as ICharacter).freeMove) {
				return false;
			}
			return true;
		},
		allowStepMove(): boolean {
			return 'freeMove' in this.obj!;
		},
		positionNames(): string[] {
			return getConstants().Base.positions;
		},

		isFirstPos(): boolean {
			return this.pos === 0;
		},

		isLastPos(): boolean {
			return this.pos === getConstants().Base.positions.length - 1;
		},
	},
});
